.container{
    width:550px;
    height: 360px;
    background-color:var(--color-White);
    left: 50%;
    top:50%;
    position: absolute;
    display: flex;
    transform: translate(-50%,-50%);
    border-radius: 20px;
    box-shadow: 5px 20px 30px hsl(241, 100%, 89%);

}


/* left
left_content */
.left{
    width: 275px;
    height: 360px;
    background-image:linear-gradient(hsl(252, 100%, 67%),hsl(241, 81%, 54%)) ;
    border-radius: 20px;
    
}

.left_content{
    display: flex;
    flex-direction: column;
    align-items: center;    
    font-family: var(--font-HankenGrotesk);
    margin: 0 20px;
}

.left_content h4{
    font-weight: 700;
    color: var(--color-Lightlavender);
}


.left_content .circle{
    width: 150px;
    height:150px;
    background-image: linear-gradient(hsla(256, 72%, 46%, 1),hsla(241, 72%, 46%, 0));
    border-radius: 100%;
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: center;
    font-family: var(--font-HankenGrotesk);
}

.left_content .circle p{
    font-size: 50px;
    color: var(--color-Paleblue);
    font-weight: 800;
    line-height: 0.5;
}

.left_content .circle p span{
    font-size: 13px;
    font-weight: 700;
    color: var(--color-Lightlavender);

}

.left_content h3{
    font-weight: 700;
    color: var(--color-Paleblue);
    font-size: 23px;
    margin: 10px 0;
}

.left_content p{
    color: var(--color-Lightlavender);
    font-size: 13.5px;
    text-align: center;
    margin: 0px 20px;
}

/* right
right_summary */

.right{
    width: 275px;
    height: 360px;
    border-radius: 20px;
}

.right_summary{
    display: flex;
    flex-direction: column;
    font-family: var(--font-HankenGrotesk);
    margin: 0px 25px;
}

.right_summary h4{
    color: var(--color-Darkgrayblue);
    margin-bottom: 10px;
}

 .right_summary div {
    display: flex;
    align-items: center;
    margin: 5px 0;
    height: 40px;
    vertical-align: middle;
    border-radius: 8px;
    justify-content: space-between;
    font-weight: 700;
}

 img{
    width:15px;
    margin: 0px 10px;
}

 .sum_reaction{
    color: var(--color-Lightred);
    background-color: #FFF6F5;
    font-size: 13.5px;
}

.sum_memory{
    color: var(--color-Orangeyyellow);
    background-color: #FFFBF2;
    font-size: 13.5px;
    

}

.sum_verbal{
    color: var(--color-Greenteal);
    background-color: #F2FBFA;
    font-size: 13.5px;

}

.sum_visual{
    color: var(--color-Cobaltblue);
    background-color: #F3F3FD;
    font-size: 13.5px;

} 

.score{
    margin :0px 10px;
    font-weight: 700;
    color: rgb(154, 149, 149);
}
.score span{
    color: var(--color-Darkgrayblue);
    font-weight: 800;
}

button{
    background-color: var(--color-Darkgrayblue);
    border: none;
    height: 40px;
    border-radius: 50px;
    color: var(--color-Paleblue);
    font-size: 13px;
    font-weight: 700;
    margin-top: 30px;
    cursor: pointer;
    transition: 0.3s;
}

button:hover{
    background-image: linear-gradient(hsl(252, 100%, 67%),hsl(241, 81%, 54%));
    box-shadow: 0px 0px 5px hsl(252, 100%, 67%);
}


    



@media  screen and (max-width:550px) {
    .container{
        width:375px;
        height: 360px;
        background-color:var(--color-White);
        left: 50%;
        top:50%;
        transition: 0.3s;
        
    }
    .left_content p{
        font-size: 12px;
        margin: 0px 10px;
    }

/* right */

    .right_summary{
        display: flex;
        flex-direction: column;
        font-family: var(--font-HankenGrotesk);
        margin: 0px 20px;
    }
    .score{
        font-size: 12px;
        transition: 0.3s;
    }
    img{
        margin: 0px 4.5px;
    }

    
}

@media screen and (max-width:420px) {
    .container{
        width: 100%;
        min-height:100%;
        flex-direction: column;
        flex-shrink: 1;
        border-radius: 0px;

    }

    .left{
        width: 100%;
        height: 350px;
        box-shadow: none;
        border-top-right-radius: 0px;
        border-top-left-radius:0px;
    }
    .left_content h4{
        font-size: larger;

    }
    .left_content h3{
        font-size: 25px;
        font-weight: 500;
    }

    .left_content .circle{
        width: 135px;
        height: 135px;
    }
    
    
    .left_content .circle p span{
        font-size: 15px;
        
    
    }
    .left_content p{
        
        font-size: 17px;
       
    }

    /* right */
    .right{
        width: 100%;
    }
    .right_summary{
        display: flex;
        flex-direction: column;
        font-family: var(--font-HankenGrotesk);
        margin: 0px 30px;
    }
    .right_summary div {
        
        margin: 10px 0;
        height: 60px;
        
    }
    
     img{
        width:20px;
        margin: 0px 15px;
    }

    .sum_reaction,.sum_memory,.sum_verbal,.sum_visual,.score{
        font-size: 18px;
    }

    button{
        height: 55px;
        font-size: 18px;
        margin-top: 10px;
        
    }
}
